import { Scrollbars } from 'react-custom-scrollbars-2';
import React from "react";
import styles from './index.module.scss';

class CustomScrollbars extends React.Component{
  render() {
    return (
      <Scrollbars 
        renderTrackHorizontal={props => <div {...props} className={styles.horizontalTrack}/>}
        renderTrackVertical={props => <div {...props} className={styles.verticalTrack}/>}
        renderThumbHorizontal={props => <div {...props} className={`${styles.thumb} ${styles.horizontal}`}/>}
        renderThumbVertical={props => <div {...props} className={`${styles.thumb} ${styles.vertical}`}/>}
        {...this.props}
      >
        {this.props.children}
      </Scrollbars>
    );
  }
}

export default CustomScrollbars;