import { useState } from 'react';

import styles from './index.module.scss';
import svgChat from '../svg/Chat';

const Chat = () => {
    const [isChat, setIsChat] = useState(true);
    const [widthZero, setWidthZero] = useState({});

    const toggleChat = () => {
        let isClose = false;

        if (!isChat) {
            setIsChat(current => !current);

            isClose = true;
        }

        setWidthZero({ width: 0 });

        setTimeout(
            () => {
                isClose ? setWidthZero({}) : setIsChat(current => !current);
            },
            isClose ? 1 : 200
        );
    }

    return (
        <div className={styles.wrapper}>
            {
                isChat ?
                    <div className={styles.chat} style={widthZero}>
                        <div className={styles.head} onClick={toggleChat}>

                        </div>
                    </div>
                    :
                    <div className={styles.tag} onClick={toggleChat}>
                        {svgChat.Chat}
                    </div>
            }
        </div>
    )
}

export default Chat;