import styles from './Social.module.scss';
import { svgCopyright } from '../../svg/Footer';
import { Email, Twitter, Meduim, Telegram } from '../../SETTINGS';

const Social = () => {
    return (

        <div className={styles.right}>
            {svgCopyright.email}

            <div className={styles.email}>
                {Email}
            </div>

            <div class={styles.links}>
                <a href={Twitter} target="_blank" rel="noreferrer">{svgCopyright.twitter}</a>
                <a href={Meduim} target="_blank" rel="noreferrer">{svgCopyright.medium}</a>
                <a href={Telegram} target="_blank" rel="noreferrer">{svgCopyright.telegram}</a>
            </div>

            <a href="//www.trustpilot.com/" target="_blank" rel="noreferrer">
                {svgCopyright.trustpilot}
            </a>
        </div>
    )
}

export default Social;