import Modal from 'react-modal';
import { useState } from 'react';

import Auth from './Auth';
import Reg from './Reg';
import Reset from './Reset';
import Wrapper from './Wrapper';
import Head from './Head';
import LoginDirectly from './Common/LoginDirectly';

import styles from './index.module.scss';

Modal.setAppElement("#root");

const Unauth = ({ onClose, typeModal, onChange }) => {

    const [outClass, setOutClass] = useState('');

    const handlerClose = () => {
        setOutClass(styles.out);

        setTimeout(() => {
            onClose();

            setOutClass('');
        }, 100);
    }

    return (
        <Modal
            isOpen={typeModal ? true : false}
            className={`${styles.modal} ${outClass}`}
            onRequestClose={handlerClose}
            overlayClassName={styles.overlay}
        >
            <Head closeClick={handlerClose}/>

            <Wrapper langCodeTitle={typeModal}>
                {
                    (typeModal === "Auth" &&
                        <Auth changeType={onChange} />)
                    || (typeModal === "Reset" &&
                        <Reset changeType={onChange} />)
                    || (typeModal === "Reg" &&
                        <Reg changeType={onChange} />)
                    || onClose()
                }

                <LoginDirectly />

            </Wrapper>
        </Modal>
    );
}

export default Unauth;