import styles from './index.module.scss';
import PaymentIcons from './PaymentIcons';
import opyright from './opyright';
import Links from './Links';

function Footer() {
    return (
        <div className={`${styles.wrapper}`}>
            <div class={styles.content}>
                <PaymentIcons />
                <Links />
                <opyright />
            </div>
        </div>
    )
}

export default Footer;