import styles from './TabSwitch.module.scss';

const TabSwitch = (props) => {
    return (        
        <div 
            className={`${styles.tabSwitch} ${props.tabType === props.active ? styles.active :''}`}
            onClick={() => props.onClick(props.tabType) }
        >
            {props.children}
        </div>
    );
}

export default TabSwitch;