import { useTranslation } from "react-i18next";

import CloseRound from '../CloseRound';

import svgGlobal from '../../svg/Global';

import styles from './Head.module.scss';

const Head = ({closeClick}) => {
    const { t } = useTranslation(null, { keyPrefix: "modal" });

    return (
        <div className={styles.head}>
            <div className={styles.welcome}>
                {t('Welcome')}
            </div>

            <div class={styles.logo}>
                {svgGlobal.LogoMini}
            </div>

            <CloseRound customClass={styles.close} closeClick={closeClick} />
        </div>
    );
}

export default Head;