import Modal from 'react-modal';
import { useState } from 'react';

import { useTranslation } from "react-i18next";

import CustomScrollbars from '../../CustomScrollbars';
import LanguageButton from './LanguageButton';
import CloseRound from '../CloseRound';

import resources from "../../LanguageInit/resources";
import styles from './index.module.scss';

Modal.setAppElement("#root");

const Language = ({ isOpen, onClose }) => {
    const { t, i18n } = useTranslation(null, { keyPrefix: 'modal.title' });

    const [outClass, setOutClass] = useState(isOpen);

    const handleLangChange = (code) => {
        //TODO   API
        i18n.changeLanguage(code);
    }

    const handlerClose = () => {
        setOutClass(styles.out);

        setTimeout(() => {
            onClose();

            setOutClass('');
        }, 100);
    }

    return (
        <Modal
            isOpen={isOpen}
            className={`${styles.modal} ${outClass}`}
            onRequestClose={handlerClose}
            overlayClassName={styles.overlay}
        >

            <div className={styles.title}>{t('Language')}</div>

            <CloseRound closeClick={handlerClose} customClass={styles.close}/>

            <div className={styles.list}>
                <CustomScrollbars>
                    <div className={styles.grid}>
                        {
                            Object.entries(resources).map(([code, lang]) => (
                                <LanguageButton
                                    svg={lang.svg}
                                    text={lang.name}
                                    codeName={code}
                                    changeLanguage={(codeReturn) => handleLangChange(codeReturn)}
                                />
                            ))
                        }
                    </div>
                </CustomScrollbars>
            </div>
        </Modal>
    );
}

export default Language;