import CustomInput from '../CustomInput';
import { useTranslation } from "react-i18next";

import styles from './PhoneInput.module.scss';
import svgAssetUnauth from '../../../svg/AssetUnauth';
import {pushNotif} from '../../../Notification';

const PhoneInput = () => {
    const { t } = useTranslation(null, {keyPrefix:'modal'});

    return (
        <>
            <div class={styles.phoneInput}>
                <CustomInput customClass={styles.first} langCodePlaceholder="+263" inputType="IDD" />
                <CustomInput customClass={styles.second} langCodePlaceholder="Phone Number" inputType="phoneNumber" />
            </div>

            <CustomInput
                langCodePlaceholder="Last 4 digit number" 
                inputType="code"
                configButton={
                    {
                        svg: svgAssetUnauth.call,
                        text: t('Call'),
                        callback: () => pushNotif.error(t('Call'))
                    }
                }
            />
        </>
    );
}

export default PhoneInput;