import { useTranslation } from "react-i18next";

import Link from './Link';

import LinksMenu from '../links/Menu';

import styles from './Links.module.scss';

const Links = () => {
    const { t } = useTranslation(null, {keyPrefix: "menu.links"});

    return (<>
        {
            LinksMenu.map((group, index) => (
                <div
                    className={`${styles.links} ${index > 0 ? styles.line : ''}`}
                >
                    {
                        group.map((link) => (
                            <Link
                                link={link.href}
                                text={t(link.text)}
                                svg={link.svg}
                            />
                        ))
                    }
                </div>
            ))
        }</>)
}

export default Links;