import { useTranslation } from "react-i18next";

import svgGlobal from '../../svg/Global';

import styles from './LanguageButton.module.scss';


const LanguageButton = ({changeLanguage, svg, text, codeName}) => {
    const { t } = useTranslation();
     
    return (
        <div
            className={
                `${styles.button} ${codeName === t('type') ? styles.selected : ''
                }`
            }
            onClick={() => changeLanguage(codeName)}
        >
            <div className={styles.flag}>
                {svg}
            </div>

            <div className={styles.name}>
                {text}
            </div>

            {
                codeName === t('type') ?
                    (
                        <div className={styles.tick}>
                            {svgGlobal.Tick}
                        </div>
                    )
                    :
                    undefined
            }
        </div>
    );
}

export default LanguageButton;