import {useState} from 'react';
import { useTranslation } from "react-i18next";

import svgAssetUnauth from '../../svg/AssetUnauth';

import PhoneInput from './Common/PhoneInput';
import CustomInput from './CustomInput';
import Form from './Common/Form';
import Button from './Common/Button';
import Buttons from './Common/Buttons';
import TopSwitch from './Common/TopSwitch';
import TabSwitch from './Common/TabSwitch';

import styles from './Auth.module.scss';

const Auth = ({changeType}) => {
    const { t } = useTranslation(null, {keyPrefix:'modal'});

    const [SwitchType, setSwitchType] = useState('Email'); 

    return (
        <>
        <TopSwitch>
            <TabSwitch active={SwitchType} tabType='Phone' onClick={setSwitchType}>
                {svgAssetUnauth.mobile}
                {t('Phone')}
            </TabSwitch>

            <TabSwitch active={SwitchType} tabType='Email' onClick={setSwitchType}>
                {svgAssetUnauth.email}                
                {t('Email')}
            </TabSwitch>
        </TopSwitch>

        <Form>
            {
                ((SwitchType === 'Phone' && <>
                    <PhoneInput />
            
                </>) || (SwitchType === 'Email' && <>

                    <CustomInput 
                        inputType="email" 
                        langCodePlaceholder="Email"
                    />
                    <CustomInput 
                        inputType="password" 
                        langCodePlaceholder="Password"
                    />

                </>) || changeType(false))
            }

            <Buttons>
                <Button green="true">
                    {svgAssetUnauth.user}
                    {t('Sign In')} 
                </Button>

                <Button onClick={() => changeType('Reg')}>
                    {svgAssetUnauth.key} 
                    {t('Create an account')}
                </Button>
            </Buttons>
        </Form>
            <div className={styles.reset} 
                onClick={() => changeType('Reset')}
            >
                {t('Forget password?')}
            </div>
        </>
    );
}

export default Auth;