import {useState} from 'react';
import { useTranslation } from "react-i18next";

import CustomScrollbars from '../CustomScrollbars';

import Buttons from "./Buttons"
import Links from './Links';
import Link from './Link';

import svgMenu from '../svg/Menu';

import styles from './index.module.scss';
import stylesLink from './Links.module.scss';

const Menu = () => {
    const { t } = useTranslation();

    const [isMenu, setIsMenu] = useState(false);  

    return (
      <>
        <div className={styles.wrapper} data-menu={isMenu ?  'open' : 'hide'}>
            <div 
                className={styles.hide} 
                onClick={() => {
                    setIsMenu(current => !current);
                }} 
            >
                {isMenu ? svgMenu.Hide : svgMenu.Show}

                <span>{t("Hiden")}</span>
            </div>

            <div className={styles.home}>
                <Link 
                    link="/" 
                    text={t('menu.links.Home')}
                    svg={svgMenu.Home} 
                    className={stylesLink.link} 
                />
            </div>

            <div className={styles.scrollBox}>
                <CustomScrollbars>
                    <Links />
                </CustomScrollbars>
            </div>

            <Buttons />
        </div>
      </>
    )
}

export default Menu;