import { About} from '../SETTINGS';
import Social from './Comma/Social';

import svgGlobal from '../svg/Global';

import styles from './opyright.module.scss';

const Copyright = () => {
    return (
        <div className={styles.wrapper}>
            <div className={styles.information}>
                {svgGlobal.Logo}

                <div className={styles.text}>{About}</div>
            </div>

            <Social />
        </div>
    )
}

export default Copyright;