import { toast } from 'react-toastify';
import styles from './index.module.scss';
import { ToastContainer } from 'react-toastify';

const autoCloseTime = 4000;

const pushNotif = {
    error: (text) => createNotif(
        text,
        (
            <svg
                xmlns="http://www.w3.org/2000/svg"
                width="25"
                height="25"
                fill="none"
                viewBox="0 0 25 25"
            >
                <path
                    fill="#F90"
                    d="M20.323 6.094L14.135 2.52a3.295 3.295 0 00-3.281 0L4.677 6.094a3.281 3.281 0 00-1.635 2.844v7.124c0 1.167.625 2.25 1.635 2.844l6.187 3.573c1.01.584 2.26.584 3.282 0l6.187-3.573a3.282 3.282 0 001.636-2.843V8.938a3.325 3.325 0 00-1.646-2.844zm-8.604 1.979c0-.427.354-.781.78-.781.428 0 .782.354.782.78v5.47a.787.787 0 01-.781.78.787.787 0 01-.781-.78v-5.47zm1.74 9.25c-.053.125-.126.24-.22.344a1.032 1.032 0 01-1.135.218 1.076 1.076 0 01-.344-.218 1.521 1.521 0 01-.229-.344 1.025 1.025 0 01-.073-.396c0-.27.104-.542.302-.74.104-.093.209-.166.344-.218a1.04 1.04 0 011.135.219c.094.104.167.208.22.343.051.125.082.26.082.396s-.03.27-.083.396z"
                ></path>
            </svg>
        )
    ),
    mess: (text) => createNotif(
        text,
        (
            <svg
                xmlns="http://www.w3.org/2000/svg"
                width="25"
                height="25"
                fill="none"
                viewBox="0 0 25 25"
            >
                <path
                    fill="#776BF8"
                    d="M19.791 8.333a3.125 3.125 0 100-6.25 3.125 3.125 0 000 6.25z"
                ></path>
                <path
                    fill="#776BF8"
                    d="M21.875 10.833v6.334c0 .145-.01.291-.02.427-.011.125-.021.24-.042.364-.01.125-.032.25-.052.365-.365 2.52-1.97 4.114-4.48 4.48-.114.02-.24.04-.364.051-.125.021-.24.031-.365.042-.135.01-.281.02-.427.02H7.834c-.146 0-.292-.01-.428-.02-.125-.01-.24-.021-.364-.042a3.842 3.842 0 01-.365-.052c-2.51-.364-4.114-1.958-4.479-4.48a3.837 3.837 0 01-.052-.364 4.338 4.338 0 01-.042-.364 5.582 5.582 0 01-.02-.427V8.875c0-.146.01-.292.02-.427.01-.125.021-.24.042-.365.01-.125.031-.25.052-.364.365-2.521 1.969-4.115 4.48-4.48.114-.02.239-.041.364-.051a5.59 5.59 0 01.792-.063h6.333a1.041 1.041 0 011.02 1.25c0 .02-.01.042-.01.063-.02.104-.031.208-.052.322a4.84 4.84 0 00.073 1.386c.031.125.052.229.094.344.083.343.219.666.396.968.062.125.146.25.218.365.344.5.771.927 1.271 1.27.115.074.24.157.365.22.302.177.625.312.969.395.114.042.218.063.343.094.48.094.948.115 1.386.073.114-.02.219-.031.323-.052.02 0 .041-.01.062-.01a1.042 1.042 0 011.25 1.02z"
                ></path>
            </svg>
        )
    ),
    success: (text) => createNotif(
        text,
        (
            <svg
                xmlns="http://www.w3.org/2000/svg"
                width="24"
                height="24"
                fill="none"
                viewBox="0 0 24 24"
            >
                <path
                    fill="#1FBF75"
                    d="M12 2C6.49 2 2 6.49 2 12s4.49 10 10 10 10-4.49 10-10S17.51 2 12 2zm4.78 7.7l-5.67 5.67a.75.75 0 01-1.06 0l-2.83-2.83a.754.754 0 010-1.06c.29-.29.77-.29 1.06 0l2.3 2.3 5.14-5.14c.29-.29.77-.29 1.06 0 .29.29.29.76 0 1.06z"
                ></path>
            </svg>
        )
    )
}

const createNotif = (text, svg) => {
    const toastId = toast(
        <>
            {svg}
            <div className={styles.content}>{text}</div>
        </>
    );

    setTimeout(function () {
        toast.dismiss(toastId);
    }, autoCloseTime);
};

const Notification = () => {
    return (
        <ToastContainer
            closeButton={false}
            className={styles.container}
            toastClassName={styles.wrapper}
            bodyClassName={styles.body}
            progressClassName={styles.progress}
            autoClose={autoCloseTime}
        />
    );
}

export {
    Notification,
    pushNotif
};