import { useTranslation } from "react-i18next";

import styles from './Form.module.scss';

const Form = ({children, hideDetails = false}) => {
    const { t } = useTranslation(null, {keyPrefix: "modal"});

    return (        
        <>
            <div className={styles.box}>
                {!hideDetails && (<div className={styles.fillDetails}>{t('Fill in the details')}</div>)}  
                  
                {children}
            </div>
        </>
    );
}

export default Form;