import { useTranslation } from "react-i18next";
import { useState } from 'react';

import inputTypes from './CustomInput/inputTypes';
import InputCheckBox from './CustomInput/InputCheckBox';
import InputButton from './CustomInput/InputButton';
import InputState from './CustomInput/InputState';

import styles from './CustomInput.module.scss';

const CustomInput = ({ customClass, langCodePlaceholder, inputType, configCheck, configButton }) => {
    const { t } = useTranslation(null, { keyPrefix: "input" });

    const def = {
        fill: false,
        bad: false,
        good: false
    };

    const [classObj, setClassObj] = useState(def);

    const handleChange = (event) => {
        const { value } = event.target;
        const pattern = event.target.getAttribute('data-pattern');

        if (value === "") return setClassObj(def);

        setClassObj(prevState => ({
            ...prevState,
            'fill': styles.fill
        }));

        if (!pattern) return;

        setClassObj(prevState => (
            new RegExp(pattern).test(value) ?
                {
                    ...prevState,
                    'good': styles.good,
                    'bad': false,
                } : {
                    ...prevState,
                    'good': false,
                    'bad': styles.bad,
                }
        ));
    };

    let classNameArr = Object.values(
        Object.assign(classObj, {
            chek: configCheck ? styles.checked : '',
            cust: customClass ? customClass : '',
            inp: styles.input
        })
    ).filter(function (v) {
        return !!v;
    }).join(' ');

    const typeInputObj = inputTypes[inputType];

    return (
        <>
            <div className={classNameArr}>

                {typeInputObj && typeInputObj.svg ?
                    <div className={styles.icon}>{typeInputObj.svg}</div>
                    : ''
                }

                <input
                    type={inputType === "Password" ? "password" : "text"}
                    placeholder={t(langCodePlaceholder)}
                    onChange={handleChange}
                    data-pattern={typeInputObj ? (typeInputObj.pattern || '') : ''}
                />

                <InputState stateType={
                        (classObj.bad && "bad") || (classObj.good && "good") || ""
                    }
                />

                {
                    configButton && <InputButton
                        onClick={() => configButton.callback()}
                    >
                        {configButton.svg || ''}
                        {configButton.text || ''}                
                    </InputButton>
                }
            </div>

            {
                configCheck && <InputCheckBox
                    stateBox={configCheck.checked}
                    stateInput={classObj.good}
                    boxText={configCheck.text}
                    onClick={
                        () => configCheck.callback(!configCheck.checked)
                    }
                >
                    {configCheck.text}
                </InputCheckBox>
            }
        </>
    );
};

export default CustomInput;