import svgGlobal from '../svg/Global';

import styles from './CloseRound.module.scss';

const CloseRound = ({closeClick, customClass = false}) => {
    return (
        <div
            onClick={closeClick}
            className={`${styles.close} ${customClass || ''}`}

        >
            {svgGlobal.Close}
        </div>
    );
}

export default CloseRound;