import { useTranslation } from "react-i18next";

import styles from './Banner.module.scss';

const Banner = () => {
    const { t } = useTranslation();
    
    return (
        <img
            class={styles.banner}
            src={process.env.PUBLIC_URL + '/images/AssetUnauth/banner-' + t('type') + '.png'}
            alt=''
        />
    );
}

export default Banner;