import {useState} from 'react';
import { useTranslation } from "react-i18next";

import svgAssetUnauth from '../../svg/AssetUnauth';

import CustomInput from './CustomInput';
import PhoneInput from './Common/PhoneInput.js';
import Form from './Common/Form';
import Button from './Common/Button';
import Buttons from './Common/Buttons';
import TopSwitch from './Common/TopSwitch';
import TabSwitch from './Common/TabSwitch';

const Reg = ({changeType}) => {
    const { t } = useTranslation(null, {keyPrefix:'modal'});

    const [SwitchType, setSwitchType] = useState('Email'); 
    const [isChecked, setIsChecked] = useState(true); 

    return (
        <>
        <TopSwitch>
            <TabSwitch active={SwitchType} tabType='Phone' onClick={setSwitchType}>
                {svgAssetUnauth.mobile}
                {t('Phone')}
            </TabSwitch>

            <TabSwitch active={SwitchType} tabType='Email' onClick={setSwitchType}>
                {svgAssetUnauth.email}                
                {t('Email')}
            </TabSwitch>
        </TopSwitch>

        <Form>
            {
                ((SwitchType === 'Phone' && <>
                    <PhoneInput />

                </>) || (SwitchType === 'Email' && <>

                    <CustomInput 
                        inputType="email" 
                        langCodePlaceholder="Email"
                        configCheck={
                            {
                                text: t('Send password on email'),
                                callback: (stateCheck) => setIsChecked(stateCheck),
                                checked: isChecked
                            }
                        }  
                    />

                    {!isChecked ? (
                        <CustomInput 
                            inputType="password" 
                            langCodePlaceholder="Password"
                        />
                    ) :''}

                </>) || changeType(false))
            }

            <CustomInput inputType="promocode" langCodePlaceholder="Promocode" />

            <Buttons>
                <Button onClick={() => changeType('Auth')}>
                    {svgAssetUnauth.user}
                    {t('Sign In')} 
                </Button>

                <Button green="true">
                    {svgAssetUnauth.key} 
                    {t('Create an account')}
                </Button>
            </Buttons>
        </Form>
        </>
    );
}

export default Reg;