import styles from './InputCheckBox.module.scss';

import svgAssetUnauth from '../../../svg/AssetUnauth';

const InputCheckBox = ({stateBox, boxText, stateInput, onClick}) => {
    return (
        
        <div className={`${styles.checkBox} ${stateInput ? styles.goodParent : ''}`} onClick={onClick}>
            <div className={styles.text}>
                {boxText}
            </div>

            {stateBox ? svgAssetUnauth.checkOn : svgAssetUnauth.checkOff}
        </div>
    );
};

export default InputCheckBox;