import { useTranslation } from "react-i18next";

import svgAssetUnauth from '../../../svg/AssetUnauth';

import styles from './LoginDirectly.module.scss';

const LoginDirectly = () => {
    const { t } = useTranslation(null, {keyPrefix: "modal"});

    return (

        <div className={styles.loginDirectly}>
            {t('Log in directly with')}

            <span>{svgAssetUnauth.metamask}</span>
            <span>{svgAssetUnauth.walletConnect}</span>
            <span>{svgAssetUnauth.telegram}</span>
            <span>{svgAssetUnauth.steam}</span>
        </div>
    );
}

export default LoginDirectly;