import {svgPaymentsCrypto, svgPaymentsFiat} from '../svg/Footer';

import styles from './PaymentIcons.module.scss';

function PaymentIcons() {
   return (
        <div className={styles.wrapper}>
            <div class={styles.icons}>
            {                
                svgPaymentsCrypto.map((icon) => icon)
            }
            </div>

            <div class={`${styles.right} ${styles.icons}`}>
            {                
                svgPaymentsFiat.map((icon) => icon)
            }
            </div>
        </div>
    )
}

export default PaymentIcons;