import { NavLink } from "react-router-dom";

import styles from './Link.module.scss';

const Link = ({link, svg, text}) => {
    return (
        <NavLink strict="true" to={link} 
            className={
                ({ isActive }) => `${styles.link} ${isActive ? styles.isActive : ''}`
            }     
            end="true"
        >
            {svg}
            <span>{text}</span>
        </NavLink>
    );
}

export default Link;